.pagination {
  font-size: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 3em;
  margin: 2em 0;

  &__button {
    font-size: 1em;
    padding: 0.8em 1.2em;
    border-radius: 1.5rem;
    font-weight: 700;
    border: 2px solid transparent;
    cursor: pointer;
    display: flex;
    align-items: center;
    column-gap: 0.5em;
    font-family: "BronovaBold", sans-serif;

    &:disabled {
      cursor: default;
    }
  }
}

@media screen and (min-width: 420px) and (max-width: 576px) {
  .pagination {
    font-size: 0.85rem;
  }
}
@media screen and (min-width: 360px) and (max-width: 421px) {
  .pagination {
    font-size: 0.75rem;
  }
}

@media screen and (max-width: 359px) {
  .pagination {
    font-size: 0.65rem;
  }
}
